import React from "react";
import { Typography, Card } from "antd";

const Pros = () => {
  return (
    <>
      <Typography.Title
        level={3}
        style={{
          textAlign: "center",
        }}
      >
         
      </Typography.Title>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <Card
          title=" "
          style={{ width: "300px", margin: "10px" }}
        >
          ...     
        </Card>
        <Card
          title=" "
          style={{ width: "300px", alignSelf: "flex-end", margin: "10px" }}
        >
          ...     
        </Card>
        <Card
          title=" "
          style={{ width: "300px", margin: "10px" }}
        >
          ...     
        </Card>
      </div>
    </>
  );
};

export default Pros;
